@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:400,700,800,900&display=swap);
.container{
  display: flex;
  justify-content: center;
  flex-direction: column;
  max-width: auto !important;
}

.presentation-card-container a, a:link, a:visited, a:hover{
  color: #13161a !important;
  font-size: 20px;
}

.perfil{
  width: 100%;
  height: 100%;
  max-height: 500px;
  max-width: 500px;
  border-radius: 50%;
  margin: 25px 0;
}

.skill-list{
  list-style-type: none;
  margin: 0;
  padding: 15px;
  width: auto;
  max-width: 700px;
  height: auto;
}

.skill{
  float: left;
  margin: 10px; 
}

.skill-icon, .social-icon, .map-icon{
  height: 50px;
  width: 50px;
}

.presentation-card-container{
  display: flex;
  width: auto;
  height: auto;
  margin-left: 10%;
  margin-right: 10%;
  padding-top: 50px;
  padding-bottom: 50px;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-direction: column;
}

.presentation-card-container h1{
  font-size: 70px;
}

.presentation-card-container h2{
  font-size: 50px;
  font-weight: normal;
  padding: 15px;
}
.presentation-card-container p{
  font-size: 30px;
  font-weight: normal;
  color: #13161a;
}

.social-link{
  margin: 10px;
}

/* Responsividade */

@media only screen and (max-width:767px) {
  .presentation-card-container a, a:link, a:visited, a:hover{
    color: #13161a !important;
    font-size: 12px;
  }

  .skill{
    font-size: 8px;
    margin: 2px;
  }

  .presentation-card-container h1{
    font-size: 35px;
    padding: 5px;
  }

  .presentation-card-container h2{
    font-size: 25px;
    padding: 5px;
  }

  .presentation-card-container p{
    font-size: 15px
  }

  .skill-icon, .social-icon, .map-icon{
    height: 30px;
    width: 30px;
  } 

  .perfil{
    border-radius: 50%;
    width: 170px;
    height: 170px;
  }
}

*{
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
}

body{
  font: bold 14px Roboto, sans-serif !important;
  background: #f2f2f2 !important;
}


.barra-superior{
  display: flex;
  align-items: center;
  background-color: #13161a !important;
}

  .barra-superior h1{
    font: bold 50px Roboto Slab, sans-serif;
    color: #f2f2f2;
    padding: 0;
    margin: 0;
  }
  .barra-superior h2{
    font-size: 25px;
    color: #f2f2f2;
    transition: opacity 0.2s;
  }
  .barra-superior h2:hover{
    opacity: 0.7;
  }

.menu{
  display: flex;
  justify-content: flex-end;
}

@media only screen and (max-width:767px){
  .barra-superior h1{
    justify-self: center;
    font-size: 40px;
  }
  .barra-superior h2{
    font-size: 20px;
  }
}
