.post-card{
  display: flex;
  width: auto;
  height: auto;
  background-color: #e2e2e2;
  margin: 2%;
  border-radius: 20px;
  padding: 0;
}

.icon-container{
  display: flex;
  padding-right: 5%;
  padding-left: 20px;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  font-size: 15px;
}

.descr-container{
  display: flex;
  justify-content: center;
  flex-direction: column;
  font-size: 18px;
  margin: 0;
  padding-top: 20px;
  padding-bottom: 20px;
  padding-right: 10px;
}

.icon{
  width: 50px;
  height: 50px;
}

.descr-container h1{
  font-size: 45px;
}

@media only screen and (max-width:767px){
  
  .descr-container h1{
    font-size: 30px;
  }

  .descr-container{
    font-size: 12px;
  }

  .icon{
    width: 40px;
    height: 40px;
  }
}
.content-container{
  padding: 5%;
}

h1{
  font-size: 60px;
  font-weight: bold;
  padding-top: 15px;
}

h2{
  font-size: 50px;
  font-weight: bold;
  padding-top: 30px;
}

h3{
  font-size: 40px;
  font-weight: bold;
  padding-top: 20px;
}

p{
  text-indent: 2em;
  font-size: 32px;
  font-weight: normal;
  text-align: justify;
}

.equacao{
  text-indent: 0;
  text-align: center;
  padding: 20px;
}

@media only screen and (max-width:767px){
  .content-container{
    padding: 2.5%;
  }

  .equacao{
    padding: 10px;
  }
  
  h1{
    font-size: 40px;
    font-weight: bold;
    padding-top: 7px;
    padding-bottom: 7px;
  }
  
  h2{
    font-size: 30px;
    font-weight: bold;
    padding-top: 15px;
  }

  h3{
    font-size: 25px;
