.content-container{
  padding: 5%;
}

h1{
  font-size: 60px;
  font-weight: bold;
  padding-top: 15px;
}

h2{
  font-size: 50px;
  font-weight: bold;
  padding-top: 30px;
}

h3{
  font-size: 40px;
  font-weight: bold;
  padding-top: 20px;
}

p{
  text-indent: 2em;
  font-size: 32px;
  font-weight: normal;
  text-align: justify;
}

.equacao{
  text-indent: 0;
  text-align: center;
  padding: 20px;
}

@media only screen and (max-width:767px){
  .content-container{
    padding: 2.5%;
  }

  .equacao{
    padding: 10px;
  }
  
  h1{
    font-size: 40px;
    font-weight: bold;
    padding-top: 7px;
    padding-bottom: 7px;
  }
  
  h2{
    font-size: 30px;
    font-weight: bold;
    padding-top: 15px;
  }

  h3{
    font-size: 25px;
    font-weight: bold;
    padding-top: 10px;
  }
  
  p{
    text-indent: 2em;
    font-size: 18px;
    font-weight: normal;
  }
}